/*
Simplify JavaScript - Browser - Cookie
	Author
		hugoalh
	Language
		JavaScript/ECMAScript 6
*/
const Support = navigator.cookieEnabled;
const Protocol = location.protocol;
const Host = location.hostname;
var ExpireDurationValue = (365.25 * 24 * 60 * 60);

/* Determine Secure */
	var Secure;
	if (Protocol == "https:") {
		Secure = true;
	} else {
		Secure = false;
	};

/* Handle Error Event */
	function OnError_NotSupport() {
		console.error("%cSimplify JavaScript (SJS) - Browser - Cookie", "font-weight: bold;", "\n", "Cookie is not enabled/supported in this browser! Ignored request.");
	};
	function OnError_Key(Method, Key) {
		console.error("%cSimplify JavaScript (SJS) - Browser - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Ignored request.");
	};
	function OnError_KeyArray(Method, Key) {
		console.error("%cSimplify JavaScript (SJS) - Browser - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
	};

/* Support Stat */
	function Stat() {
		return Support;
	};

/* Expire Duration */
	function ExpireDuration(MX = null) {
		if (typeof MX == "number" && Number.isSafeInteger(MX) && MX > 0) {
			ExpireDurationValue = MX;
		} else if (MX == null) {
			return ExpireDurationValue;
		} else {
			console.warn("[Simplify JavaScript - Browser - Cookie] Invalid expire duration \"" + JSON.stringify(MX) + "\"! Expire duration must be type of number, safe integer and larger than 0! Ignored request.");
		};
	};

/* Internal Function (Do not modify) */
	function Set_Internal(Key, Value, Decay = false) {
		let ExpireTime;
		if (Decay == true) {
			let OldTime = new Date(0);
			ExpireTime = OldTime.toUTCString();
		} else {
			let CurrentTime = new Date();
			CurrentTime.setTime(CurrentTime.getTime() + (ExpireDurationValue * 1000));
			ExpireTime = CurrentTime.toUTCString();
		};
		let FullCookie = encodeURIComponent(Key) + "=" + encodeURIComponent(Value) + "; expires=" + ExpireTime + "; domain=" + Host + "; path=/";
		if (Secure == true) {
			FullCookie += "; secure";
		};
		document.cookie = FullCookie;
	};

/* Set */
	function Set(Key = null, Value = null) {
		if (Support == true) {
			if (typeof Key == "string" && (typeof Value == "string" || typeof Value == "number" || typeof Value == "boolean")) {
				Set_Internal(Key, Value, false);
			} else {
				if (typeof Key != "string") {
					OnError_Key("Set", Key);
				};
				if (typeof Value != "string" && typeof Value != "number" && typeof Value != "boolean") {
					console.warn("[Simplify JavaScript - Browser - Cookie] Invalid value \"" + JSON.stringify(Value) + "\" in \"Set\" request! Value must be type of string/number/boolean! Ignored request.");
				};
			};
		} else {
			OnError_NotSupport();
		};
	};

/*Get*/
	function Get(Key = null) {
		if (Support == true) {
			let CookieList = document.cookie;
			CookieList = CookieList.split(";");
			let CookiePair = {};
			if (CookieList.length > 0) {
				for (let index = 0; index < CookieList.length; index++) {
					let CookiePiece = CookieList[index].split("=");
					while (CookiePiece[0].charAt(0) == " ") {
						CookiePiece[0] = CookiePiece[0].substring(1);
					};
					CookiePair[decodeURIComponent(CookiePiece[0])] = decodeURIComponent(CookiePiece[1]);
				};
			};
			if (typeof Key == "string") {
				return CookiePair[Key];
			} else if (Array.isArray(Key) == true) {
				let ValueArray = [];
				for (let index = 0; index < Key.length; index++) {
					if (typeof Key[index] == "string") {
						ValueArray.push(CookiePair[Key[index]]);
					} else {
						OnError_KeyArray("Get", Key[index]);
						ValueArray.push(undefined);
					};
				};
				return ValueArray;
			} else if (Key == null) {
				return CookiePair;
			} else {
				OnError_Key("Get", Key);
				return undefined;
			};
		} else {
			OnError_NotSupport();
			return undefined;
		};
	};

/*Delete*/
	function Delete(Key = null) {
		if (Support == true) {
			if (typeof Key == "string") {
				Set_Internal(Key, 0, true);
			} else if (Array.isArray(Key) == true) {
				for (let index = 0; index < Key.length; index++) {
					if (typeof Key[index] == "string") {
						Set_Internal(Key[index], 0, true);
					} else {
						OnError_KeyArray("Delete", Key[index]);
					};
				};
			} else {
				OnError_Key("Delete", Key);
			};
		} else {
			OnError_NotSupport();
		};
	};
	function Del(Key = null) {
		return Delete(Key);
	};
	function DeleteAll() {
		if (Support == true) {
			let KeyArray = Object.keys(Get());
			for (let index = 0; index < KeyArray.length; index++) {
				Set_Internal(KeyArray[index], 0, true);
			};
		} else {
			OnError_NotSupport();
		};
	};
	function DelAll() {
		return DeleteAll();
	};

/* Rebirth */
	function Rebirth() {
		if (Support == true) {
			let CookiePair = Get();
			let KeyArray = Object.keys(CookiePair);
			let ValueArray = Object.values(CookiePair);
			for (let index = 0; index < KeyArray.length; index++) {
				Set(KeyArray[index], ValueArray[index]);
			};
		} else {
			OnError_NotSupport();
		};
	};

/* Package Module */
	export {
		Stat,
		ExpireDuration,
		Set,
		Get,
		Delete,
		Del,
		DeleteAll,
		DelAll,
		Rebirth
	};
