/* Root */
	Math.root = function(Value, Root) {
		return Math.pow(Value, (1/Root));
	};

/* Greatest Common Divisor */
	Math.gcd = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		for (let index = 0; index < Chain.length; index++) {
			if (typeof Chain[index] != "number") {
				return undefined;
			};
		};
		if (Chain.length == 2) {
			if (Chain[1] == 0) {
				return Chain[0];
			} else {
				return Math.gcd(Chain[1], Chain[0] % Chain[1]);
			};
		} else if (Chain.length > 2) {
			var result = Math.gcd(Chain[0], Chain[1]);
			for (let index = 2; index < Chain.length; index++) {
				result = Math.gcd(result, Chain[index]);
			};
			return result;
		} else {
			return undefined;
		};
	};
	Math.gcf = Math.gcd; // Greatest Common Factor
	Math.hcf = Math.gcd; // Highest Common Factor
	Math.gcm = Math.gcd; // Greatest Common Measure
	Math.hcd = Math.gcd; // Highest Common Divisor

/* Least Common Multiple */ // Lowest Common Multiple
	Math.lcm = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		for (let index = 0; index < Chain.length; index++) {
			if (typeof Chain[index] != "number") {
				return undefined;
			};
		};
		if (Chain.length >= 2) {
			let Top = 1;
			for (let index = 0; index < Chain.length; index++) {
				Top *= Chain[index];
			};
			let Bottom = Math.gcd(Chain);
			return (Math.abs(Top) / Bottom);
		} else {
			return undefined;
		};
	};
	Math.scm = Math.lcm; // Smallest Common Multiple

/* Hypotenuse */
	Math.hypotenuse = function(A, B, Query = "h") {
		switch (Query) {
			case "h":
				return Math.sqrt((A * A) + (B * B));
				break;
			case "c":
				return Math.sqrt((A * A) - (B * B));
				break;
			default:
				return undefined;
				break;
		};
	};
	Math.antihypotenuse = function(A, B) {
		return Math.hypotenuse(A, B, "c");
	};
