/* Handle Error */
	function OnError_InputValue(ConverterName, InputValue, InputValueType) {
		console.error("%cSimplify JavaScript (SJS) - Converter", "font-weight: bold;", "\n", "Unknown input value \"" + JSON.stringify(InputValue) + "\" in the \"" + ConverterName + "\" converter! Input value must be type of " + InputValueType + "! Ignored request.");
	};
	function OnError_InputUnit(ConverterName, InputUnit) {
		console.error("%cSimplify JavaScript (SJS) - Converter", "font-weight: bold;", "\n", "Unknown input unit \"" + JSON.stringify(InputUnit) + "\" in the \"" + ConverterName + "\" converter! Input unit must be a standard unit! Ignored request.");
	};
	function OnError_OutputUnit(ConverterName, OutputUnit) {
		console.error("%cSimplify JavaScript (SJS) - Converter", "font-weight: bold;", "\n", "Unknown output unit \"" + JSON.stringify(OutputUnit) + "\" in the \"" + ConverterName + "\" converter! Output unit must be a standard unit! Ignored request.");
	};

function Temperature(InputValue, InputUnit, OutputUnit = "K") {
	let SIValue, OutputValue;
	if (typeof InputValue == "number") {
		switch (InputUnit) {
			case "K": case "Kelvin": // SIUnit
				SIValue = InputValue;
				break;
			case "C": case "°C": case "℃": case "Celsius":
				SIValue = InputValue + 273.15;
				break;
			case "F": case "°F": case "℉": case "Fahrenheit":
				SIValue = (InputValue + 459.67) * (5/9);
				break;
			case "R": case "°R": case "Rankine":
				SIValue = InputValue * (5/9);
				break;
			case "De": case "°De": case "Delisle":
				SIValue = 373.15 - InputValue * (2/3);
				break;
			case "N": case "°N": case "Newton":
				SIValue = InputValue * (100/33) + 273.15;
				break;
			case "Re": case "°Re": case "Ré": case "°Ré": case "Réaumur": case "Reaumur":
				SIValue = InputValue * (5/4) + 273.15;
				break;
			case "Ro": case "°Ro": case "Rø": case "°Rø": case "Rømer": case "Romer":
				SIValue = (InputValue - 7.5) * (40/21) + 273.15;
				break;
			default:
				OnError_InputUnit("Temperature", InputUnit);
				OutputValue = undefined;
				break;
		};
		switch (OutputUnit) {
			case "K": case "Kelvin": // SIUnit
				OutputValue = SIValue;
				break;
			case "C": case "°C": case "℃": case "Celsius":
				OutputValue = SIValue - 273.15;
				break;
			case "F": case "°F": case "℉": case "Fahrenheit":
				OutputValue = SIValue * (9/5) - 459.67;
				break;
			case "R": case "°R": case "Rankine":
				OutputValue = SIValue * (9/5);
				break;
			case "De": case "°De": case "Delisle":
				OutputValue = (373.15 - SIValue) * (3/2);
				break;
			case "N": case "°N": case "Newton":
				OutputValue = (SIValue - 273.15) * (33/100);
				break;
			case "Re": case "°Re": case "Ré": case "°Ré": case "Réaumur": case "Reaumur":
				OutputValue = (SIValue - 273.15) * (4/5);
				break;
			case "Ro": case "°Ro": case "Rø": case "°Rø": case "Rømer": case "Romer":
				OutputValue = (SIValue - 273.15) * (21/40) + 7.5;
				break;
			default:
				OnError_OutputUnit("Temperature", OutputUnit);
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Temperature", InputValue, "number");
		OutputValue = undefined;
	};
	if (OutputValue == null) {
		OutputValue = undefined;
	};
	return OutputValue;
};

function Length(InputValue, InputUnit, OutputUnit = "m") {
	let SIValue, OutputValue;
	if (typeof InputValue == "number") {
		switch (InputUnit) {
			case "m": case "Metre": case "Meter": // SIUnit
				SIValue = InputValue;
				break;
			case "Ym": case "Yottametre": case "Yottameter":
				SIValue = InputValue * Math.pow(10, 24);
				break;
			case "Zm": case "Zettametre": case "Zettameter":
				SIValue = InputValue * Math.pow(10, 21);
				break;
			case "Em": case "Exametre": case "Exameter":
				SIValue = InputValue * Math.pow(10, 18);
				break;
			case "Pm": case "Petametre": case "Petameter":
				SIValue = InputValue * Math.pow(10, 15);
				break;
			case "Tm": case "Terametre": case "Terameter":
				SIValue = InputValue * Math.pow(10, 12);
				break;
			case "Gm": case "Gigametre": case "Gigameter":
				SIValue = InputValue * Math.pow(10, 9);
				break;
			case "Mm": case "Megametre": case "Megameter":
				SIValue = InputValue * Math.pow(10, 6);
				break;
			case "km": case "㎞": case "㏎": case "Kilometre": case "Kilometer":
				SIValue = InputValue * Math.pow(10, 3);
				break;
			case "hm": case "Hectometre": case "Hectometer":
				SIValue = InputValue * Math.pow(10, 2);
				break;
			case "dam": case "Decametre": case "Decameter":
				SIValue = InputValue * Math.pow(10, 1);
				break;
			case "dm": case "Decimetre": case "Decimeter":
				SIValue = InputValue * Math.pow(10, -1);
				break;
			case "cm": case "㎝": case "Centimetre": case "Centimeter":
				SIValue = InputValue * Math.pow(10, -2);
				break;
			case "mm": case "㎜": case "Millimetre": case "Millimeter":
				SIValue = InputValue * Math.pow(10, -3);
				break;
			case "um": case "µm": case "Micrometre": case "Micrometer":
				SIValue = InputValue * Math.pow(10, -6);
				break;
			case "nm": case "Nanometre": case "Nanometer":
				SIValue = InputValue * Math.pow(10, -9);
				break;
			case "pm": case "Picometre": case "Picometer":
				SIValue = InputValue * Math.pow(10, -12);
				break;
			case "fm": case "Femtometre": case "Femtometer":
				SIValue = InputValue * Math.pow(10, -15);
				break;
			case "am": case "Attometre": case "Attometer":
				SIValue = InputValue * Math.pow(10, -18);
				break;
			case "zm": case "Zeptometre": case "Zeptometer":
				SIValue = InputValue * Math.pow(10, -21);
				break;
			case "ym": case "Yoctometre": case "Yoctometer":
				SIValue = InputValue * Math.pow(10, -24);
				break;
			case "in": case "Inch":
				SIValue = InputValue * 0.0254;
				break;
			case "yd": case "Yard":
				SIValue = InputValue * 0.9144;
				break;
			case "A": case "Å": case "Ångström": case "Angstrom": 
				SIValue = InputValue * Math.pow(10, -10);
				break;
			case "ft": case "Foot":
				SIValue = InputValue * 0.3048;
				break;
			case "mi": case "Mile":
				SIValue = InputValue * 1609.344;
				break;
			case "nmi": case "NM": case "M": case "Nautical":
				SIValue = InputValue * 1852;
				break;
			case "ly": case "LightYear":
				SIValue = InputValue * (9.4607 * Math.pow(10, 15));
				break;
			default:
				OnError_InputUnit("Length", InputUnit);
				OutputValue = undefined;
				break;
		};
		switch (OutputUnit) {
			case "m": case "Metre": case "Meter": // SIUnit
				OutputValue = SIValue;
				break;
			case "Ym": case "Yottametre": case "Yottameter":
				OutputValue = SIValue / Math.pow(10, 24);
				break;
			case "Zm": case "Zettametre": case "Zettameter":
				OutputValue = SIValue / Math.pow(10, 21);
				break;
			case "Em": case "Exametre": case "Exameter":
				OutputValue = SIValue / Math.pow(10, 18);
				break;
			case "Pm": case "Petametre": case "Petameter":
				OutputValue = SIValue / Math.pow(10, 15);
				break;
			case "Tm": case "Terametre": case "Terameter":
				OutputValue = SIValue / Math.pow(10, 12);
				break;
			case "Gm": case "Gigametre": case "Gigameter":
				OutputValue = SIValue / Math.pow(10, 9);
				break;
			case "Mm": case "Megametre": case "Megameter":
				OutputValue = SIValue / Math.pow(10, 6);
				break;
			case "km": case "㎞": case "㏎": case "Kilometre": case "Kilometer":
				OutputValue = SIValue / Math.pow(10, 3);
				break;
			case "hm": case "Hectometre": case "Hectometer":
				OutputValue = SIValue / Math.pow(10, 2);
				break;
			case "dam": case "Decametre": case "Decameter":
				OutputValue = SIValue / Math.pow(10, 1);
				break;
			case "dm": case "Decimetre": case "Decimeter":
				OutputValue = SIValue / Math.pow(10, -1);
				break;
			case "cm": case "㎝": case "Centimetre": case "Centimeter":
				OutputValue = SIValue / Math.pow(10, -2);
				break;
			case "mm": case "㎜": case "Millimetre": case "Millimeter":
				OutputValue = SIValue / Math.pow(10, -3);
				break;
			case "um": case "µm": case "Micrometre": case "Micrometer":
				OutputValue = SIValue / Math.pow(10, -6);
				break;
			case "nm": case "Nanometre": case "Nanometer":
				OutputValue = SIValue / Math.pow(10, -9);
				break;
			case "pm": case "Picometre": case "Picometer":
				OutputValue = SIValue / Math.pow(10, -12);
				break;
			case "fm": case "Femtometre": case "Femtometer":
				OutputValue = SIValue / Math.pow(10, -15);
				break;
			case "am": case "Attometre": case "Attometer":
				OutputValue = SIValue / Math.pow(10, -18);
				break;
			case "zm": case "Zeptometre": case "Zeptometer":
				OutputValue = SIValue / Math.pow(10, -21);
				break;
			case "ym": case "Yoctometre": case "Yoctometer":
				OutputValue = SIValue / Math.pow(10, -24);
				break;
			case "in": case "Inch":
				OutputValue = SIValue / 0.0254;
				break;
			case "yd": case "Yard":
				OutputValue = SIValue / 0.9144;
				break;
			case "A": case "Å": case "Ångström": case "Angstrom": 
				OutputValue = SIValue / Math.pow(10, -10);
				break;
			case "ft": case "Foot":
				OutputValue = SIValue / 0.3048;
				break;
			case "mi": case "Mile":
				OutputValue = SIValue / 1609.344;
				break;
			case "nmi": case "NM": case "M": case "Nautical":
				OutputValue = SIValue / 1852;
				break;
			case "ly": case "LightYear":
				OutputValue = SIValue / (9.4607 * Math.pow(10, 15));
				break;
			default:
				OnError_OutputUnit("Length", OutputUnit);
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Length", InputValue, "number");
		OutputValue = undefined;
	};
	if (OutputValue == null) {
		OutputValue = undefined;
	};
	return OutputValue;
};

function Angle(InputValue, InputUnit, OutputUnit = "R") {
	let SIValue, OutputValue;
	if (typeof InputValue == "number") {
		switch (InputUnit) {
			case "rad": case "R": case "RAD": case "Radian": case "radian": // SIUnit
				SIValue = InputValue;
				break;
			case "°": case "D": case "Degree": case "degree":
				SIValue = InputValue * (Math.PI/180);
				break;
			case "g": case "G": case "gon": case "ᵍ": case "grad": case "grade": case "Gradian": case "gradian":
				SIValue = InputValue * (Math.PI/200);
				break;
			case "tr": case "T": case "pla": case "Turn": case "turn":
				SIValue = InputValue * (2*Math.PI);
				break;
			default:
				OnError_InputUnit("Angle", InputUnit);
				OutputValue = undefined;
				break;
		};
		switch (OutputUnit) {
			case "rad": case "R": case "RAD": case "Radian": case "radian": // SIUnit
				OutputValue = SIValue;
				break;
			case "°": case "D": case "Degree": case "degree":
				OutputValue = SIValue / (Math.PI/180);
				break;
			case "g": case "G": case "gon": case "ᵍ": case "grad": case "grade": case "Gradian": case "gradian":
				OutputValue = SIValue / (Math.PI/200);
				break;
			case "tr": case "T": case "pla": case "Turn": case "turn":
				OutputValue = SIValue / (2*Math.PI);
				break;
			default:
				OnError_OutputUnit("Angle", OutputUnit);
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Angle", InputValue, "number");
		OutputValue = undefined;
	};
	if (OutputValue == null) {
		OutputValue = undefined;
	};
	return OutputValue;
};

function Weight(InputValue, InputUnit, OutputUnit = "kg") {
	let SIValue, OutputValue;
	if (typeof InputValue == "number") {
		switch (InputUnit) {
			case "kg": case "㎏": case "Kilogram": // SIUnit
				SIValue = InputValue;
				break;
			case "g": case "Gram":
				SIValue = InputValue / 1000;
				break;
			case "t": case "Tonne":
			case "Mg": case "Megagram":
				SIValue = InputValue * 1000;
				break;
			case "斤": case "catty": case "Catty":
				SIValue = InputValue * 0.60478982;
				break;
			case "lb": case "Pound":
				SIValue = InputValue / 2.20462262;
				break;
			case "兩": case "Tael":
				SIValue = InputValue / 16 * 0.60478982;
				break;
			case "錢": case "Mace":
				SIValue = InputValue / 160 * 0.60478982;
				break;
			case "oz": case "OZ": case "Ounce":
				SIValue = InputValue * 28.349523125 / 1000;
				break;
			case "eV": case "ElectronVolt":
				SIValue = InputValue * (1.783 * Math.pow(10, -36));
				break;
			default:
				OnError_InputUnit("Weight", InputUnit);
				OutputValue = undefined;
				break;
		};
		switch (OutputUnit) {
			case "kg": case "㎏": case "Kilogram": // SIUnit
				OutputValue = SIValue;
				break;
			case "g": case "Gram":
				OutputValue = SIValue * 1000;
				break;
			case "t": case "Tonne":
			case "Mg": case "Megagram":
				OutputValue = SIValue / 1000;
				break;
			case "斤": case "catty": case "Catty":
				OutputValue = SIValue / 0.60478982;
				break;
			case "lb": case "Pound":
				OutputValue = SIValue * 2.20462262;
				break;
			case "兩": case "Tael":
				OutputValue = SIValue / 0.60478982 * 16;
				break;
			case "錢": case "Mace":
				OutputValue = SIValue / 0.60478982 * 160;
				break;
			case "oz": case "OZ": case "Ounce":
				OutputValue = SIValue / 28.349523125 * 1000;
				break;
			case "eV": case "ElectronVolt":
				OutputValue = SIValue / (1.783 * Math.pow(10, -36));
				break;
			default:
				OnError_OutputUnit("Weight", OutputUnit);
				OutputValue = undefined;
				break;
		};
	} else {
		OnError_InputValue("Weight", InputValue, "number");
		OutputValue = undefined;
	};
	if (OutputValue == null) {
		OutputValue = undefined;
	};
	return OutputValue;
};

/* Package Module */
	export {
		Temperature,
		Length,
		Angle,
		Weight
	};
