/*
Simplify JavaScript - Browser - Information
	Author
		hugoalh
	Language
		JavaScript/ECMAScript 6
*/
const Data = {
	"Agent": navigator.userAgent,
	"DeviceType": "",
	"Width": innerWidth,
	"Height": innerHeight,
	"Orientation": "",
	"OperatingSystem": "",
	"Language": (navigator.language || navigator.userLanguage || navigator.browserLanguage || navigator.systemLanguage).toLowerCase(),
	"BrandName": ""
};
const List = {
	"Bot": [
		"bot",
		"generator",
		"http",
		".com",
		".net",
		".org",
		".io",
		"preview",
		"crawler",
		"google page speed insight"
	],
	"Television": [
		"viera",
		"netcast",
		"boxee",
		"kylo",
		"roku",
		"dlnadoc",
		"ce-html"
	]
};

/* Internal Search Service */
	function InternalSearch_AgentStandard(Query) {
		if (Data["Agent"].search(Query) != -1) {
			return true;
		} else {
			return false;
		};
	};
	function InternalSearch_AgentLowerCase(Query) {
		if (Data["Agent"].toLowerCase().search(Query) != -1) {
			return true;
		} else {
			return false;
		};
	};

/* Determine Device Type */
	for (let index = 0; index < List["Bot"].length; index++) {
		if (InternalSearch_AgentLowerCase(List["Bot"][index]) == true) {
			Data["DeviceType"] = "Bot";
		};
	};
	function DetermineDeviceType_Television_Internal() {
		let Result = false;
		for (let index = 0; index < List["Television"].length; index++) {
			if (InternalSearch_AgentLowerCase(List["Television"][index]) == true) {
				Result = true;
			};
		};
		return Result;
	};
	if (Data["DeviceType"] != "Bot") {
		if (typeof window.process === "object") {
			Data["DeviceType"] = "Node Webkit";
		} else if (InternalSearch_AgentStandard("TV") == true || DetermineDeviceType_Television_Internal() == true) {
			Data["DeviceType"] = "Television";
		} else if (InternalSearch_AgentLowerCase("mobile") == true) {
			Data["DeviceType"] = "Mobile";
		} else if (InternalSearch_AgentLowerCase("tablet") == true || InternalSearch_AgentLowerCase("touch") == true) {
			Data["DeviceType"] = "Tablet";
		} else if (InternalSearch_AgentLowerCase("kiosk") == true) {
			Data["DeviceType"] = "Kiosk";
		} else {
			Data["DeviceType"] = "Desktop";
		};
	};

/* Determine Brand Name */
	if (Data["DeviceType"] == "Bot") {
		Data["BrandName"] = "Bot";
	} else {
		if (InternalSearch_AgentStandard("Firefox") == true) {
			Data["BrandName"] = "Mozilla Firefox";
		} else if (InternalSearch_AgentStandard("Edge") == true) {
			Data["BrandName"] = "Microsoft Edge";
		} else if (InternalSearch_AgentStandard("Chrome") == true) {
			Data["BrandName"] = "Google Chrome";
		} else if (InternalSearch_AgentStandard("Safari") == true) {
			Data["BrandName"] = "Apple Safari";
		} else {
			Data["BrandName"] = "Unknown";
		};
	};

/* Determine Operating System */
	if (InternalSearch_AgentStandard("Windows") == true) {
		Data["OperatingSystem"] = "Windows";
	} else if (InternalSearch_AgentStandard("Macintosh") == true|| InternalSearch_AgentStandard("Mac OS") == true || InternalSearch_AgentStandard("OS X") == true) {
		Data["OperatingSystem"] = "Macintosh";
	} else if (InternalSearch_AgentStandard("Android") == true) {
		Data["OperatingSystem"] = "Android";
	} else if (InternalSearch_AgentStandard("CrOS") == true) {
		Data["OperatingSystem"] = "ChromeOS";
	} else if (InternalSearch_AgentStandard("Linux") == true) {
		Data["OperatingSystem"] = "Linux";
	} else {
		Data["OperatingSystem"] = "Unknown";
	};

/* Handle Size/Resize */
	/* Determine Orientation */
		function DetermineOrientation_Internal() {
			if (Data["Width"] < Data["Height"]) {
				Data["Orientation"] = "Portrait";
			} else {
				Data["Orientation"] = "Landscape";
			};
		};
	/* On Resize */
		addEventListener("resize", function() {
			Data["Width"] = innerWidth;
			Data["Height"] = innerHeight;
			DetermineOrientation_Internal();
		});

function Agent() {
	return Data["Agent"];
};
function DeviceType(Query = null) {
	if (Query == null) {
		return Data["DeviceType"];
	} else if (Data["DeviceType"].search(Query) != -1) {
		return true;
	} else {
		return false;
	};
};
function Width() {
	return Data["Width"];
};
function Height() {
	return Data["Height"];
};
function Orientation() {
	return Data["Orientation"];
};
function OperatingSystem(Query = null) {
	if (Query == null) {
		return Data["OperatingSystem"];
	} else if (Data["OperatingSystem"].search(Query) != -1) {
		return true;
	} else {
		return false;
	};
};
function OS(Query = null) {
	return OperatingSystem(Query);
};
function Language(Query = null) {
	if (Query == null) {
		return Data["Language"];
	} else if (Data["Language"].search(Query) != -1) {
		return true;
	} else {
		return false;
	};
};
function BrandName(Query = null) {
	if (Query == null) {
		return Data["BrandName"];
	} else if (Data["BrandName"].search(Query) != -1) {
		return true;
	} else {
		return false;
	};
};

/* Package Module */
	export {
		Agent,
		DeviceType,
		Width,
		Height,
		Orientation,
		OperatingSystem,
		OS,
		Language,
		BrandName
	};
